const app = getApp();
export default {
	data () {
		return {
			globalData:{},
			mainList: [
				{ status: '我的会员', id:1},
				// { status: '缴费记录', id:2},
				{ status: '我的资料', id:2},
				{ status: '我的报名', id:3},
				// { status: '我的积分', id:5},
				// { status: '我的订单', id:6},
				// { status: '我的推荐码', id:7},
			],
		}
	},
	onLoad(options) {
	    this.globalData = app.globalData
	},
	// 模态框显示隐藏
	showModal(e) {
	    this.setData({
	      modalName: e.currentTarget.dataset.target
	    })
	},
	hideModal(e) {
	    this.setData({
	      modalName: null
	    })
	},
	onTabItemTap() {
	    if (app.globalData.userInfo){
	       return
	    }
	    uni.showModal({
	        title: '提示',
	        content: '您尚未注册，是否马上去注册页面？',
	        success: function (sm) {
				if (sm.confirm) {
				    uni.redirectTo({
				       url: '/pages/application/member',
				    })
				} else{
				    uni.switchTab({
				       url: '/pages/index/index'
				    })
				}
			},
		})
	},
	methods: {
		puer(id) {
			if (id == 1) {
				uni.navigateTo({
					url: '/pages/myhome/dues'
				})
			} 
		},
		navck(id) {
			if (id == 2) {
				uni.navigateTo({
					url: '/pages/myhome/means/means'
				})
			} 
			else if (id == 3) {
				uni.navigateTo({
					url: '/pages/myhome/taken/enroll'
				})
			}
		}
	}
}
